  async cambioclave() {



     const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Cambio de Contraseña',
      inputs: [
        {
          name: 'clave1',
          placeholder: 'Ingrese Contraseña nueva',
          type: 'password',
          value: ''
        },
        {
          name: 'clave2',
          placeholder: 'Ingrese Confirmación de Contraseña nueva',
          type: 'password',
          value: ''
        }
      ],
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (todo) => {

            console.log(todo);
          }
        }, {
          text: 'Actualizar Contraseña',
          handler: (todo) => {
            if (todo) {

              if (todo.clave1 === todo.clave2) {
                const credentials = {
                  idempresa: this.userinfo[0].idempresa._id,
                  email         	:  this.userinfo[0].email    	,
                  password: todo.clave1,
                  bitacora: {idempresa: this.userinfo[0].idempresa._id, idafiliado: '', email: this.userinfo[0].email
                  , permiso: 'Actualiza', accion: 'Cambia clave'}
                };


                this.authenticationService.createAPISINTOKEN(credentials, '/api/auth/register2').then((result) => {
                  this.authenticationService.presentAlert('Exito', 'Operación realizada con exito.', '');
                }, (err) => {
                  this.authenticationService.presentAlert('Advertencia', 'OperaciónEmail ya esta asociado una cuenta existente', '');
                });

              } else {
                this.authenticationService.presentAlert('Advertencia', 'Contraseñas no coinciden.', '');
              }
              /*




*/

            }

          }
        }
      ]
    });

    await alert.present().then(() => {
      const firstInput: any = document.querySelector('ion-alert password');
      firstInput.focus();
      return;
    });


    





  }
